/**
 * UI
 */
import React from 'react'
import './Add.css'
export default ({onAddClickHandler}) => {
    return (
        <div className="App-header">
            <div id="add-good">
                <h4></h4>
                <input className="good-control-input" type="text" placeholder="URL" ref={(input) => { window.textInput = input; } } />
            <span className="input-group-btn"> <button className="btn btn-default" onClick={() => onAddClickHandler(window.textInput.value)}></button></span>
            </div>
        </div>
    )
}


